import { request } from 'utils';

const prefix = '/api/students';

export const getTeacherOwnerClassActivity = ({
  classId,
  staffId,
  yearEduId,
  semesterId,
} = {}) =>
  request({
    prefix,
    url: '/Portal/GetTeacherOwnerClassActivity',
    method: 'GET',
    params: {
      classId,
      staffId,
      yearEduId,
      semesterId,
    },
    data: {},
  });




